.advantage {
	margin-top: 70px;
	&__wrap {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	&__item {
		text-align: center;
		margin-bottom: 30px;
	}
	&__img {
		position: relative;
		margin: 0 auto;
		// width: 80px;
		// height: 80px;
		width: 130px;
		height: 130px;
		background: #ffffff;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		border-radius: 50%;
		& img {
			position: absolute;
			width: 70%;
			height: 70%;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}
	&__title {
		margin: 10px 0 8px 0;
		font-weight: 700;
		font-size: 1.2rem;
		line-height: 15px;
		color: #676767;
	}
	&__descr {
		max-width: 300px;
		font-weight: 400;
		font-size: 1rem;
		line-height: 12px;
		text-align: center;
		color: #808080;
	}
}

@media (min-width: 961px) {
	.advantage {
		&__wrap {
			flex-direction: row;
			justify-content: space-between;
			align-items: start;
			margin: 0 -15px;
		}
		&__item {
			flex: 0 1 25%;
			margin: 0 15px;
		}
	}
}
@media (min-width: 1025px) {
	.advantage {
		margin-top: 140px;
		&__wrap {
			align-items: stretch;
		}
		&__item {
			padding: 35px;
			flex: 0 1 248px;
			background: #ffffff;
			box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
			border-radius: 25px;
		}
		&__img {
			box-shadow: none;
			width: 148px;
			height: 148px;
			& img {
				width: 100%;
				height: 100%;
			}
		}
	}
}
