// .modifier .modifier .modifier .modifier .modifier .modifier .modifier .modifier .mod

.modifier {
	margin-top: 100px;
	&__img {
		position: relative;
		width: 90%;
		margin: 40px auto;
		padding-bottom: 36%;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}

.config {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	&__item {
		max-width: 320px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin: 10px 0;
		padding: 10px;
		background: #ffffff;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		border-radius: 25px;
		&_rewers {
			flex-direction: row-reverse;
		}
	}
	&__text-block {
		text-align: right;
		margin-right: 25px;
		&_left {
			text-align: left;
			margin: 0px 0px 0px 25px;
		}
	}
	&__title {
		margin-bottom: 9px;
		color: #676767;
		font-size: 1.4rem;
		font-weight: 500;
	}
	&__descr {
		font-size: 1.2rem;
		color: #999999;
	}
	&__img {
		flex: 0 0 78px;
		position: relative;
		height: 78px;
		border-radius: 50%;
		overflow: hidden;
		& img {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 100%;
			height: 100%;
		}
	}
}

.config__item_active {
	background-color: rgba($color: $green, $alpha: 0.4);
}

@media (min-width: 641px) {
	.modifier {
		max-width: 1030px;
		&__img {
			margin: 20px auto;
		}
	}
	.config {
		flex-direction: row;
		justify-content: end;
		&::after {
			position: absolute;
			z-index: 4;
			content: "";
			width: 91px;
			height: 170px;
			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
		}
		&_roof {
			margin-right: 100px;
			&::after {
				right: -90px;
				top: 40%;
				background-image: url(../img/icons/arrow-left.svg);
			}
		}
		&_wall {
			justify-content: start;
			margin-left: 110px;
			&::after {
				left: -100px;
				bottom: 40%;
				background-image: url(../img/icons/arrow-right.svg);
			}
		}
		&__item {
			background-color: transparent;
			box-shadow: none;
			flex: 0 1 98px;
			margin: 0;
		}
		&__text-block {
			display: none;
		}
	}
	.config__item_active {
		flex: 0 1 320px;
		padding: 10px 15px;
		background: #ffffff;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		border-radius: 100px;
	}
	.config__item_active .config__text-block {
		display: inline-block;
	}
	.config__item_active .config__title {
		font-size: 1.1rem;
	}
	.config__title {
		font-size: 0.9rem;
	}
}

// .stage .stage .stage .stage .stage .stage .stage .stage .stage .stage .stage .stage

.stage {
	&__wrap {
		margin-top: 100px;
	}
	&__left-block {
		display: none;
	}
}

.dots-slider {
	overflow: hidden;
	margin: 0 auto 30px;
	text-align: center;
	height: 60px;
	max-width: 641px;
	width: 100%;
	&__slid {
		position: relative;
		cursor: pointer;
		flex: 1 0 40px;
		&::before,
		&::after {
			content: "";
			position: absolute;
			z-index: -1;
			width: 50%;
			height: 4px;
			background-color: #cdcdcd;
			top: 16px;
			transform: translateY(-50%);
			transition: all 0.3s ease-in-out;
		}
		&::before {
			left: 0px;
		}
		&::after {
			right: 0px;
		}
		&_first {
			&::before {
				display: none;
			}
		}
		&_last {
			&::after {
				display: none;
			}
		}
	}
	&__dot {
		margin: 0 auto;
		width: 32px;
		height: 32px;
		background-color: #fff;
		border: solid 8px #cdcdcd;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		border-radius: 50%;
		transition: all 0.3s ease-in-out;
	}
	&__number {
		margin-top: 10px;
		font-size: 0rem;
	}
}
.dots-slider .swiper-slide-thumb-active::before,
.dots-slider .swiper-slide-thumb-active::after {
	background-color: $green;
}
.dots-slider .swiper-slide-thumb-active .dots-slider__dot {
	background-color: $green;
	border: solid 8px #c7f9cc;
}

.dots-slider .swiper-slide-thumb-active .dots-slider__number {
	font-weight: 500;
	font-size: 1.2rem;
	line-height: 15px;
	color: $green;
}

.stage-slider {
	padding: 10px;
	position: relative;
	max-width: 641px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden;
	background-color: transparent;
	&__slid {
		display: flex;
		flex-direction: column-reverse;
		padding: 30px;
		background: #ffffff;
		box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
		border-radius: 17px;
		height: auto;
	}
	&__text-block {
		margin-top: 40px;
	}
	&__title {
		margin-bottom: 14px;
		font-weight: 600;
		font-size: 1.4rem;
		line-height: 17px;
		color: #000000;
	}
	&__text {
		font-size: 1.2rem;
		line-height: 15px;
		color: #808080;
		margin-bottom: 34px;
	}
	&__img {
		position: relative;
		margin: 0 auto;
		width: 200px;
		height: 160px;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__buttons .swiper-button-next {
		position: absolute;
		z-index: 11;
		left: 30px;
		bottom: 30px;
		padding-right: 39px;
		background-image: url(../img/icons/arrows-next-stage.svg);
		background-repeat: no-repeat;
		background-position: 117px 5px;
		font-weight: 600;
		font-size: 1.2rem;
		line-height: 15px;
		color: $green;
		background-color: transparent;
	}
}

@media (min-width: 1025px) {
	.stage {
		&__wrap {
			display: flex;
			justify-content: space-between;
			align-items: end;
		}
		&__left-block {
			display: block;
			flex: 0 1 350px;
			padding: 76px 32px 64px 32px;
			background: #fff;
			box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
			border-radius: 25px;
			margin-right: 25px;
			text-align: center;
		}
		&__right-block {
			flex: 0 1 800px;
			margin-left: 25px;
		}
	}

	.left-block {
		&__img {
			position: relative;
			width: 140px;
			height: 186px;
			margin: 0 auto;
			& img {
				position: absolute;
				width: 100%;
				height: 100%;
				right: 0;
				top: 0;
				object-fit: contain;
			}
		}
		&__descr {
			margin-top: 30px;
			font-size: 1.4rem;
			line-height: 24px;
			color: #808080;
		}
	}

	.stage-slider {
		max-width: 730px;
		padding: 0;
		border-radius: 25px;
		&__slid {
			flex-direction: row;
			padding: 50px;
		}
		&__text-block {
			flex: 0 0 260px;
		}
		&__title {
			font-size: 2.5rem;
			line-height: 30px;
		}
		&__text {
			margin-top: 30px;
			font-size: 1.4rem;
			line-height: 17px;
		}
		&__img {
			width: 330px;
			height: 250px;
		}
		&__buttons .swiper-button-next {
			left: 50px;
		}
	}
}
