@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Light.woff") format("woff"), url("../fonts/Montserrat-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  transition: all 0s ease 0s;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "Montserrat";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "Montserrat";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html {
  font-size: 10px;
}

body {
  color: #0f0f0f;
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 400;
  background-color: #efefef;
}

body._lock {
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

._container {
  max-width: 1324px;
  padding: 0 15px;
  margin: 0 auto;
}

.grey-shadow {
  box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
}

.header__wrap {
  height: 95px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  position: relative;
  overflow: hidden;
  width: 69px;
  height: 28px;
}

.header__logo img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__action {
  display: flex;
  align-items: center;
}

.header__btn {
  display: none;
}

.header__phone,
.header__map {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #ffffff;
  box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
  overflow: hidden;
}

.header__phone:hover,
.header__map:hover {
  box-shadow: 0px 1px 10px rgb(176, 176, 176);
}

.header__phone {
  margin-right: 10px;
}

.header__phone a {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 0;
  background-image: url(../img/icons/phone.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 17px 17px;
}

.header__map {
  width: 36px;
  height: 36px;
  font-size: 0;
  background-image: url(../img/icons/location.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 17px 21px;
}

.intro {
  display: flex;
  flex-direction: column-reverse;
}

.intro__text-block {
  margin-top: 43px;
  text-align: center;
}

.intro__text-wrap {
  margin: 0 auto;
}

.intro__title {
  font-weight: 800;
  font-size: 3.2rem;
  line-height: 3.5rem;
  color: #1e1e1e;
}

.intro__descr {
  margin: 12px 0 30px 0;
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: #808080;
}

.intro__action {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.intro__layout,
.intro__download {
  margin: 0 25px;
  display: inline-block;
  width: 34px;
  height: 34px;
  background: #ffffff;
  box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
  border-radius: 50%;
  font-size: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 17px 17px;
}

.intro__layout:hover,
.intro__download:hover {
  box-shadow: 0px 1px 10px rgb(176, 176, 176);
}

.intro__layout {
  background-image: url(../img/icons/layout.svg);
}

.intro__download {
  background-image: url(../img/icons/download.svg);
}

.intro__img-block {
  position: relative;
  width: 100%;
  padding-bottom: 63%;
  overflow: hidden;
}

.intro__img-block img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.intro__mortgage {
  position: absolute;
  bottom: 15px;
  right: 20px;
  padding: 10px 20px;
  background: #fff;
  box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
  border-radius: 100px;
  font-weight: 600;
  font-size: 1.3rem;
  color: #09d25a;
}

.intro__mortgage:hover {
  box-shadow: 0px 1px 10px rgb(176, 176, 176);
}

.advantage {
  margin-top: 70px;
}

.advantage__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantage__item {
  text-align: center;
  margin-bottom: 30px;
}

.advantage__img {
  position: relative;
  margin: 0 auto;
  width: 130px;
  height: 130px;
  background: #ffffff;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
  border-radius: 50%;
}

.advantage__img img {
  position: absolute;
  width: 70%;
  height: 70%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.advantage__title {
  margin: 10px 0 8px 0;
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 15px;
  color: #676767;
}

.advantage__descr {
  max-width: 300px;
  font-weight: 400;
  font-size: 1rem;
  line-height: 12px;
  text-align: center;
  color: #808080;
}

.modifier {
  margin-top: 100px;
}

.modifier__img {
  position: relative;
  width: 90%;
  margin: 40px auto;
  padding-bottom: 36%;
}

.modifier__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.config {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.config__item {
  max-width: 320px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
  padding: 10px;
  background: #ffffff;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
  border-radius: 25px;
}

.config__item_rewers {
  flex-direction: row-reverse;
}

.config__text-block {
  text-align: right;
  margin-right: 25px;
}

.config__text-block_left {
  text-align: left;
  margin: 0px 0px 0px 25px;
}

.config__title {
  margin-bottom: 9px;
  color: #676767;
  font-size: 1.4rem;
  font-weight: 500;
}

.config__descr {
  font-size: 1.2rem;
  color: #999999;
}

.config__img {
  flex: 0 0 78px;
  position: relative;
  height: 78px;
  border-radius: 50%;
  overflow: hidden;
}

.config__img img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}

.config__item_active {
  background-color: rgba(9, 210, 90, 0.4);
}

.stage__wrap {
  margin-top: 100px;
}

.stage__left-block {
  display: none;
}

.dots-slider {
  overflow: hidden;
  margin: 0 auto 30px;
  text-align: center;
  height: 60px;
  max-width: 641px;
  width: 100%;
}

.dots-slider__slid {
  position: relative;
  cursor: pointer;
  flex: 1 0 40px;
}

.dots-slider__slid::before,
.dots-slider__slid::after {
  content: "";
  position: absolute;
  z-index: -1;
  width: 50%;
  height: 4px;
  background-color: #cdcdcd;
  top: 16px;
  transform: translateY(-50%);
  transition: all 0.3s ease-in-out;
}

.dots-slider__slid::before {
  left: 0px;
}

.dots-slider__slid::after {
  right: 0px;
}

.dots-slider__slid_first::before {
  display: none;
}

.dots-slider__slid_last::after {
  display: none;
}

.dots-slider__dot {
  margin: 0 auto;
  width: 32px;
  height: 32px;
  background-color: #fff;
  border: solid 8px #cdcdcd;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}

.dots-slider__number {
  margin-top: 10px;
  font-size: 0rem;
}

.dots-slider .swiper-slide-thumb-active::before,
.dots-slider .swiper-slide-thumb-active::after {
  background-color: #09d25a;
}

.dots-slider .swiper-slide-thumb-active .dots-slider__dot {
  background-color: #09d25a;
  border: solid 8px #c7f9cc;
}

.dots-slider .swiper-slide-thumb-active .dots-slider__number {
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 15px;
  color: #09d25a;
}

.stage-slider {
  padding: 10px;
  position: relative;
  max-width: 641px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  background-color: transparent;
}

.stage-slider__slid {
  display: flex;
  flex-direction: column-reverse;
  padding: 30px;
  background: #ffffff;
  box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
  border-radius: 17px;
  height: auto;
}

.stage-slider__text-block {
  margin-top: 40px;
}

.stage-slider__title {
  margin-bottom: 14px;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 17px;
  color: #000000;
}

.stage-slider__text {
  font-size: 1.2rem;
  line-height: 15px;
  color: #808080;
  margin-bottom: 34px;
}

.stage-slider__img {
  position: relative;
  margin: 0 auto;
  width: 200px;
  height: 160px;
  overflow: hidden;
}

.stage-slider__img img {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.stage-slider__buttons .swiper-button-next {
  position: absolute;
  z-index: 11;
  left: 30px;
  bottom: 30px;
  padding-right: 39px;
  background-image: url(../img/icons/arrows-next-stage.svg);
  background-repeat: no-repeat;
  background-position: 117px 5px;
  font-weight: 600;
  font-size: 1.2rem;
  line-height: 15px;
  color: #09d25a;
  background-color: transparent;
}

footer {
  height: 200px;
}

@media (min-width: 641px) {
  .modifier {
    max-width: 1030px;
  }

  .modifier__img {
    margin: 20px auto;
  }

  .config {
    flex-direction: row;
    justify-content: end;
  }

  .config::after {
    position: absolute;
    z-index: 4;
    content: "";
    width: 91px;
    height: 170px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  .config_roof {
    margin-right: 100px;
  }

  .config_roof::after {
    right: -90px;
    top: 40%;
    background-image: url(../img/icons/arrow-left.svg);
  }

  .config_wall {
    justify-content: start;
    margin-left: 110px;
  }

  .config_wall::after {
    left: -100px;
    bottom: 40%;
    background-image: url(../img/icons/arrow-right.svg);
  }

  .config__item {
    background-color: transparent;
    box-shadow: none;
    flex: 0 1 98px;
    margin: 0;
  }

  .config__text-block {
    display: none;
  }

  .config__item_active {
    flex: 0 1 320px;
    padding: 10px 15px;
    background: #ffffff;
    box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
    border-radius: 100px;
  }

  .config__item_active .config__text-block {
    display: inline-block;
  }

  .config__item_active .config__title {
    font-size: 1.1rem;
  }

  .config__title {
    font-size: 0.9rem;
  }
}

@media (min-width: 961px) {
  .header__logo {
    width: 145px;
    height: 56px;
  }

  .header__btn {
    display: block;
    height: 56px;
    padding: 0 32px;
    border-radius: 100px;
    background: #09d25a;
    box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
    overflow: hidden;
    color: #fff;
    font-weight: 600;
    font-size: 2rem;
    line-height: 24px;
  }

  .header__btn:hover {
    box-shadow: 0px 1px 10px rgb(176, 176, 176);
  }

  .header__phone,
  .header__map {
    width: 56px;
    height: 56px;
    background-size: 26px 32px;
  }

  .header__phone {
    margin: 0 34px 0 53px;
  }

  .header__phone a {
    background-size: 32px 32px;
  }

  .advantage__wrap {
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
    margin: 0 -15px;
  }

  .advantage__item {
    flex: 0 1 25%;
    margin: 0 15px;
  }
}

@media (min-width: 1025px) {
  .header__wrap {
    height: 146px;
  }

  .header__phone {
    width: auto;
    border-radius: 100px;
    padding: 16px 32px;
  }

  .header__phone a {
    width: auto;
    background-image: none;
    font-weight: 600;
    font-size: 2rem;
    line-height: 24px;
    color: #1e1e1e;
  }

  .intro {
    flex-direction: row;
  }

  .intro__text-wrap {
    text-align: left;
    width: 535px;
  }

  .intro__title {
    font-size: 8rem;
    line-height: 90px;
  }

  .intro__descr {
    margin: 38px 0 60px;
    font-size: 2.8rem;
    line-height: 34px;
  }

  .intro__action {
    justify-content: flex-start;
  }

  .intro__layout {
    display: block;
    margin: 0px;
    width: auto;
    height: 100%;
    width: 100%;
    padding: 18px 36px;
    background-image: none;
    background: #09d25a;
    box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
    border-radius: 110px;
    color: #fff;
    font-size: 2.2rem;
  }

  .intro__download {
    height: 63px;
    background-size: 24px 24px;
    flex: 0 0 63px;
  }

  .intro__img-block {
    padding-bottom: 30%;
  }

  .advantage {
    margin-top: 140px;
  }

  .advantage__wrap {
    align-items: stretch;
  }

  .advantage__item {
    padding: 35px;
    flex: 0 1 248px;
    background: #ffffff;
    box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
    border-radius: 25px;
  }

  .advantage__img {
    box-shadow: none;
    width: 148px;
    height: 148px;
  }

  .advantage__img img {
    width: 100%;
    height: 100%;
  }

  .stage__wrap {
    display: flex;
    justify-content: space-between;
    align-items: end;
  }

  .stage__left-block {
    display: block;
    flex: 0 1 350px;
    padding: 76px 32px 64px 32px;
    background: #fff;
    box-shadow: 0px 3px 13px rgba(176, 176, 176, 0.35);
    border-radius: 25px;
    margin-right: 25px;
    text-align: center;
  }

  .stage__right-block {
    flex: 0 1 800px;
    margin-left: 25px;
  }

  .left-block__img {
    position: relative;
    width: 140px;
    height: 186px;
    margin: 0 auto;
  }

  .left-block__img img {
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    top: 0;
    object-fit: contain;
  }

  .left-block__descr {
    margin-top: 30px;
    font-size: 1.4rem;
    line-height: 24px;
    color: #808080;
  }

  .stage-slider {
    max-width: 730px;
    padding: 0;
    border-radius: 25px;
  }

  .stage-slider__slid {
    flex-direction: row;
    padding: 50px;
  }

  .stage-slider__text-block {
    flex: 0 0 260px;
  }

  .stage-slider__title {
    font-size: 2.5rem;
    line-height: 30px;
  }

  .stage-slider__text {
    margin-top: 30px;
    font-size: 1.4rem;
    line-height: 17px;
  }

  .stage-slider__img {
    width: 330px;
    height: 250px;
  }

  .stage-slider__buttons .swiper-button-next {
    left: 50px;
  }
}