//<ПЕРЕМЕННЫЕ>=============================================================
$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1324;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

$green: #09d25a;
$base: #0f0f0f;
$grey: #999999;
// $secondary-text: #606774;
// $black: #202328;
//</ПЕРЕМЕННЫЕ>============================================================

//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>=============================================
@import "help/null";
html {
	font-size: 10px;
}
body {
	color: $base;
	font-size: 1.6rem;
	font-style: normal;
	font-weight: 400;
	background-color: #efefef;

	&._lock {
		overflow: hidden;
	}
}
//</ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>============================================

//<ОБОЛОЧКА>================================================================
.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
//</ОБОЛОЧКА>===============================================================

//</ОСНОВНАЯ СЕТКА>=========================================================
._container {
	max-width: $maxWidthContainer + px;
	padding: 0 15px;
	margin: 0 auto;
}
//</ОСНОВНАЯ СЕТКА>=========================================================

