// .header .header .header .header .header .header .header .header .header .header .header .header
.header {
	&__wrap {
		height: 95px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__logo {
		position: relative;
		overflow: hidden;
		width: 69px;
		height: 28px;
		& img {
			position: absolute;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__action {
		display: flex;
		align-items: center;
	}
	&__btn {
		display: none;
	}

	&__phone,
	&__map {
		width: 36px;
		height: 36px;
		border-radius: 50%;
		background: #ffffff;
		box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
		overflow: hidden;
		&:hover {
			box-shadow: 0px 1px 10px rgba(176, 176, 176, 1);
		}
	}
	&__phone {
		margin-right: 10px;
		& a {
			display: block;
			width: 100%;
			height: 100%;
			font-size: 0;
			background-image: url(../img/icons/phone.svg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: 17px 17px;
		}
	}
	&__map {
		width: 36px;
		height: 36px;
		font-size: 0;
		background-image: url(../img/icons/location.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 17px 21px;
	}
}
@media (min-width: 961px) {
	.header {
		&__logo {
			width: 145px;
			height: 56px;
		}
		&__btn {
			display: block;
			height: 56px;
			padding: 0 32px;
			border-radius: 100px;
			background: $green;
			box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
			overflow: hidden;
			color: #fff;
			font-weight: 600;
			font-size: 2rem;
			line-height: 24px;
			&:hover {
				box-shadow: 0px 1px 10px rgba(176, 176, 176, 1);
			}
		}
		&__phone,
		&__map {
			width: 56px;
			height: 56px;
			background-size: 26px 32px;
		}
		&__phone {
			margin: 0 34px 0 53px;
			& a {
				background-size: 32px 32px;
			}
		}
	}
}
@media (min-width: 1025px) {
	.header {
		&__wrap {
			height: 146px;
		}
		&__phone {
			width: auto;
			border-radius: 100px;
			padding: 16px 32px;
			& a {
				width: auto;
				background-image: none;
				font-weight: 600;
				font-size: 2rem;
				line-height: 24px;
				color: #1e1e1e;
			}
		}
	}
}
