// .intro .intro .intro .intro .intro .intro .intro .intro .intro .intro .intro .intro .intro .intro
.intro {
	display: flex;
	flex-direction: column-reverse;
	&__text-block {
		margin-top: 43px;
		text-align: center;
	}
	&__text-wrap {
		margin: 0 auto;
	}
	&__title {
		font-weight: 800;
		font-size: 3.2rem;
		line-height: 3.5rem;
		color: #1e1e1e;
	}
	&__descr {
		margin: 12px 0 30px 0;
		font-size: 1.2rem;
		line-height: 1.5rem;
		color: #808080;
	}
	&__action {
		margin-top: 30px;
		display: flex;
		justify-content: center;
	}
	&__layout,
	&__download {
		margin: 0 25px;
		display: inline-block;
		width: 34px;
		height: 34px;
		background: #ffffff;
		box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
		border-radius: 50%;
		font-size: 0;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 17px 17px;
		&:hover {
			box-shadow: 0px 1px 10px rgba(176, 176, 176, 1);
		}
	}
	&__layout {
		background-image: url(../img/icons/layout.svg);
	}
	&__download {
		background-image: url(../img/icons/download.svg);
	}
	&__img-block {
		position: relative;
		width: 100%;
		padding-bottom: 63%;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__mortgage {
		position: absolute;
		bottom: 15px;
		right: 20px;
		padding: 10px 20px;
		background: #fff;
		box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
		border-radius: 100px;
		font-weight: 600;
		font-size: 1.3rem;
		color: $green;
		&:hover {
			box-shadow: 0px 1px 10px rgba(176, 176, 176, 1);
		}
	}
}
@media (min-width: 1025px) {
	.intro {
		flex-direction: row;
		&__text-wrap {
			text-align: left;
			width: 535px;
		}
		&__title {
			font-size: 8rem;
			line-height: 90px;
		}
		&__descr {
			margin: 38px 0 60px;
			font-size: 2.8rem;
			line-height: 34px;
		}
		&__action {
			justify-content: flex-start;
		}
		&__layout {
			display: block;
			margin: 0px;
			width: auto;
			height: 100%;
			width: 100%;
			padding: 18px 36px;
			background-image: none;
			background: $green;
			box-shadow: 0px 1px 5px rgba(176, 176, 176, 0.35);
			border-radius: 110px;
			color: #fff;
			font-size: 2.2rem;
		}
		&__download {
			height: 63px;
			background-size: 24px 24px;
			flex: 0 0 63px;
		}
		&__img-block {
			padding-bottom: 30%;
		}
	}
}
